___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --main-color-darkmode: rgb(39, 39, 39);
  --font-color-darkmode: rgb(109, 197, 255);
  --card-color-darkmode: rgb(95, 94, 94);
}

:root.special {
  --main-color-darkmode: rgb(255, 255, 255);
  --font-color-darkmode: rgb(0, 0, 0);
  --card-color-darkmode: rgb(221, 221, 221);
}
/* 
*{
    border: solid red 1px;
} */

#content {
  height: fit-content;
  display: grid;
  grid-template-columns: 1fr 6fr;
  grid-template-rows: 100px 12fr 50px;
}

header {
  background-color: var(--main-color-darkmode);
  color: var(--font-color-darkmode);
  grid-column: 1/3;
  grid-row: 1/2;
}

#sidebar {
  background-color: var(--main-color-darkmode);
  color: var(--font-color-darkmode);
  grid-column: 1/2;
  grid-row: 2/3;
}

main {
  grid-column: 2/3;
  grid-row: 2/3;
}

footer {
  background-color: var(--main-color-darkmode);
  color: var(--font-color-darkmode);
  grid-column: 1/3;
  grid-row: 3/4;
}

/* Main content */
main {
  background-color: var(--main-color-darkmode);
  color: var(--font-color-darkmode);
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  /* flex-direction: column; */
}

.jobCard {
  background-color: var(--card-color-darkmode);
  border-radius: 12px;
  box-shadow: 10px;
  justify-self: center;
  margin: 20px;
  padding: 20px;
  min-height: 150px;
  max-width: 200px;
  width: 1fr;
}

.projectForm {
  display: flex;
  height: 600px;
  width: 600px;
  flex-direction: column;
  justify-content: space-around;
}

.button {
  max-width: 100px;
  max-height: 100px;
}

#projectButton {
  justify-self: center;
  align-self: center;
}
